/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react-hooks/exhaustive-deps */
import {SearchOutlined, SyncOutlined} from "@ant-design/icons";
import {Button, Form, Grid, Input, Table} from "@pankod/refine-antd";
import {useNavigation} from "@pankod/refine-core";
import {Col, message, Row, Spin, Tooltip} from "@pankod/refine-antd";
import {gqlDataProvider} from "api";
import {PermissionData} from "apps/core/interfaces";
import {useEffect, useState} from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import ReactTimeAgo from "react-time-ago";

interface PermissionSearchFormData {
  key: string;
}

interface Props {
  height?: any;
  canAdd?: boolean;
}

interface permissionFormData {
  name: string;
}

export const ControlPermissionsComponent: React.FC<Props> = (props: Props) => {
  const [permissions, setPermissions] = useState<PermissionData[]>([]);
  const [start, setStart] = useState(1);
  const [total, setTotal] = useState(0);
  const [limit, setLimit] = useState(20);
  const [searchKey, setSearchKey] = useState("");
  const [hasNext, setHasNext] = useState(false);
  const [loading, setLoading] = useState(true);
  const {push} = useNavigation();
  const [searchForm] = Form.useForm<PermissionSearchFormData>();
  const [form] = Form.useForm<permissionFormData>();
  const breakpoint = Grid.useBreakpoint();
  const isMobile = !breakpoint.lg;

  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      fixed: true,
      render: (text: any, row: any, index: any) => (
        <span>
          <a>{row.name}</a>
        </span>
      ),
    },
    {
      title: "Code",
      dataIndex: "code",
      fixed: true,
      render: (text: any, row: any, index: any) => (
        <span>
          <a>{text}</a>
        </span>
      ),
    },
    {
      title: "Created",
      dataIndex: "created",
      render: (text: any, row: any, index: any) => (
        <>
          <span>
            <ReactTimeAgo date={new Date(row.created)} />
          </span>
        </>
      ),
    },
    {
      title: "Status",
      dataIndex: "isActive",
      render: (text: any, row: any, index: any) => (
        <a>
          {row?.isActive ? (
            <span style={{color: "green"}}>Active</span>
          ) : (
            <span style={{color: "red"}}>Inactive</span>
          )}
        </a>
      ),
    },
    {
      title: "",
      dataIndex: "action",
      render: (text: any, row: any, index: any) => (
        <div style={{display: "flex", justifyContent: "flex-end"}}></div>
      ),
    },
  ];

  const getSystemPermissions = async (
    start: number,
    key: string = "",
    limit: number = 20
  ) => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "get",
      metaData: {
        operation: "systemPermissions",
        variables: {
          page: {value: start, type: "Int", required: true},
          key: {value: key, type: "String", required: false},
          pageSize: {value: limit, type: "Int", required: false},
        },
        fields: [
          "total",
          "page",
          "pages",
          "hasNext",
          "hasPrev",
          {
            results: [
              "id",
              "name",
              "code",
              "isActive",
              "created",
              "updated",
              "canEdit",
              "canDelete",
              "canManage",
            ],
          },
        ],
      },
    });
    setStart(data.page);
    setTotal(data.total);
    setHasNext(data.hasNext);
    setPermissions(data.results);
    setLoading(false);
  };

  const syncSystemPermissions = async () => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "syncSystemPermissions",
        variables: {},
        fields: ["success", "message"],
      },
    });

    if (data.success) {
      message.success(data.message);
      permissions.length = 0;
      setPermissions([]);
      getSystemPermissions(1);
    } else {
      message.error(data.message);
    }
  };

  useEffect(() => {
    getSystemPermissions(1, "", 20);
  }, []);

  return (
    <>
      <Row style={{marginTop: 10}}>
        <Col span={isMobile ? 24 : 20}>
          <Form<PermissionSearchFormData>
            layout="vertical"
            form={searchForm}
            onFinish={(values) => {
              setStart(1);
              permissions.length = 0;
              setSearchKey(values.key);
              getSystemPermissions(1, values.key, limit);
            }}
          >
            <Form.Item name="key">
              <Input
                size="large"
                placeholder="Search ..."
                prefix={<SearchOutlined />}
                autoComplete="off"
                allowClear
              />
            </Form.Item>
          </Form>
        </Col>

        <Col
          span={isMobile ? 24 : 4}
          style={{display: "flex", justifyContent: "flex-end"}}
        >
          <Tooltip title={"Synchronize Default School Ownerships"}>
            <Button
              size="large"
              icon={<SyncOutlined />}
              onClick={() => syncSystemPermissions()}
            ></Button>
          </Tooltip>
        </Col>
      </Row>

      <div>
        <Table
          loading={loading}
          columns={columns}
          dataSource={permissions}
          scroll={{x: true}}
          pagination={{
            onChange: (page, pageSize) => {
              setLimit(pageSize);
              getSystemPermissions(page, searchKey, pageSize);
            },
            total: total,
            pageSize: limit,
            position: ["bottomCenter"],
            showQuickJumper: true,
          }}
        />
      </div>
    </>
  );
};
