import {
  Authenticated,
  LayoutWrapper,
  useGetIdentity,
} from "@pankod/refine-core";
import {Breadcrumb} from "antd";
import {Link} from "react-router-dom";
import {ControlPermissionsComponent} from "../components/control_permissions_component";
import {ControlLayout} from "../control_layout";
import {ControlHeader} from "../header";
import {useEffect} from "react";
import urlAppendString from "services/url_append_string";
import {ControlPermitted} from "../components/permissions/control_permission";

const ControlPermissions: React.FC = () => {
  return (
    <div style={{marginTop: 10}}>
      <Breadcrumb>
        <Breadcrumb.Item>
          <Link to={"/control/analytics"}>Home</Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>Permissions</Breadcrumb.Item>
      </Breadcrumb>
      <ControlPermissionsComponent height={80} />
    </div>
  );
};

export const ControlPermissionsPage = () => {
  const {data: user} = useGetIdentity();

  const checkIfUserIsAdmin = () => {
    if (user?.isStaff === false || user?.isAdmin === false) {
      window.location.pathname = "/home";
    }
  };
  useEffect(() => {
    checkIfUserIsAdmin();
    window.history.pushState({}, "", urlAppendString());
  }, []);
  return (
    <ControlLayout>
      <ControlPermitted>
        <ControlPermissions />
      </ControlPermitted>
    </ControlLayout>
  );
};
